body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App:before {
  text-align: center;
  min-height: 100vh;
  background-color:'transparent';
  content: "";
  display: block;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -10;
  background: url(/static/media/Head.ac75bc35.png) no-repeat center bottom;
  background-size: 100vmin ;
}


.HeadImg{
  height:100vh;
  

}
#pinContainer {
  height: 100vh;
  width: 100vw;
  overflow: hidden;
 /** background: white url('./assets/Head.png') no-repeat fixed center 100%;
  background-size: 100vmin ;*/
}
#pinContainer .panel {
  height: 100vh;
  width: 100vw;
  position: absolute;
  text-align: center;
}
.panel span {
  position: relative;
  display: block;
  top: 50%;
  font-size: 80px;
  
}

.panel.blue {
  background-color:transparent;

}

.panel.turqoise {
  background-color: transparent;
}

.panel.green {
  background-color: transparent;
}

.panel.bordeaux {
  background-color: transparent;
}


